<template>
    <div class="content-manage-box">
        <div class="ibox">
            <div class="ibox-title">
                <h3>
                    <div class="pull-right">
                        <el-tooltip class="item" effect="dark" content=" 5000 " placement="top">
                            <el-button @click="handleExportData($event)" size="small" icon="upload"></el-button>
                        </el-tooltip>
                    </div>
                    
                    <el-tooltip :content="'' + (filter.content_type == 1 ? '' : '') "
                                placement="top">
                        <el-switch
                                v-model="filter.content_type"
                                @change="filterAction()"
                                on-color="#13ce66"
                                off-color="#ff4949"
                                on-text=""
                                off-text=""
                                on-value="1"
                                off-value="3">
                        </el-switch>
                    </el-tooltip>
                </h3>
            </div>
            <div class="ibox-filter">
                <el-form :inline="true" label-position="top">
                    <el-form-item label="">
                        <el-input placeholder=""
                                  size="small"
                                  @keyup.enter.native="filterAction()"
                                  v-model="filter.title">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="ID">
                        <el-input placeholder="ID"
                                  size="small"
                                  @keyup.enter.native="filterAction()"
                                  v-model="filter.id">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input placeholder=""
                                  size="small"
                                  @keyup.enter.native="filterAction()"
                                  v-model="filter.source_name">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="ID">
                        <el-input placeholder="ID"
                                  size="small"
                                  @keyup.enter.native="filterAction()"
                                  v-model="filter.admin_member_id">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-checkbox v-model="filter.is_hot" @change="filterAction()" true-label="2" false-label="0">
                            
                        </el-checkbox>
                        <el-checkbox v-model="filter.is_top" @change="filterAction()" true-label="2" false-label="0">
                            
                        </el-checkbox>
                    </el-form-item>
                </el-form>
            </div>
            <div class="ibox-filter">
                <el-form :inline="true" label-position="top">
                    <!--<el-form-item label="">-->
                        <!--<el-select-->
                            <!--size="small"-->
                            <!--@change="filterAction()"-->
                            <!--v-model="filter.source_name_type">-->
                            <!--<el-option label="" value="0"></el-option>-->
                            <!--<el-option-->
                                <!--v-for="(item, $key) in sourceNameTypes"-->
                                <!--:label="item"-->
                                <!--:value="$key"-->
                                <!--:key="$key">-->
                            <!--</el-option>-->
                        <!--</el-select>-->
                    <!--</el-form-item>-->

                    <el-form-item label="">
                        <el-select
                                size="small"
                                @change="filterAction()"
                                v-model="filter.type">
                            <el-option label="" value=""></el-option>
                            <el-option
                                    v-for="item in articleTypes"
                                    :label="item.name"
                                    :value="item.id.toString()"
                                    :key="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="">
                        <article-tags
                                v-model="filter.tag"
                                @change="filterAction()"
                                type="article"
                                :category="filter.type">
                        </article-tags>
                    </el-form-item>

                    <el-form-item label="">
                        <el-select
                                size="small"
                                v-model="filter.status"
                                @change="filterAction()">
                            <el-option label="" value=""></el-option>
                            <el-option
                                    v-for="(item, $key) in articleStatus"
                                    :label="item"
                                    :value="$key"
                                    :key="item"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="tips">
                        <el-select
                                size="small"
                                v-model="filter.tips"
                                @change="filterAction()">
                            <el-option label="" value=""></el-option>
                            <el-option
                                    v-for="item in articleTips"
                                    :label="item"
                                    :value="item"
                                    :key="item">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="">
                        <el-select
                                size="small"
                                v-model="filter.is_delay"
                                @change="filterAction()">
                            <el-option label="" value=""></el-option>
                            <el-option
                                    v-for="(item, $key) in articleDelay"
                                    :label="item"
                                    :value="$key"
                                    :key="item">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="">
                        <el-button @click="filterAction($event)" type="primary" icon="search"></el-button>
                        &nbsp;&nbsp;&nbsp;
                        <router-link :to="{ name: 'PushContentEdit', query: { type: filter.type } }">
                            <el-button type="info" icon="edit"> </el-button>
                        </router-link>
                    </el-form-item>

                </el-form>
            </div>
            <div class="ibox-filter">
                <el-popover
                        ref="modifyCatePopover"
                        placement="top"
                        width="200"
                        v-model="modifyCate.visible">
                    <div>
                        
                        <el-select
                                v-model="modifyCate.type">
                            <el-option label="" value=""></el-option>
                            <el-option
                                    v-for="item in articleTypes"
                                    :label="item.name"
                                    :value="item.id"
                                    :key="item.id"></el-option>
                        </el-select>
                    </div>
                    <div style="text-align: right; margin: 0">
                        <el-button size="mini" type="text" @click="modifyCate.visible = false"></el-button>
                        <el-button type="primary" size="mini" @click="handleBatchAction($event, 'batchEditCategory', { modifyCate: modifyCate, articleTypesMap: articleTypesMap })"></el-button>
                    </div>
                </el-popover>

                <span class="el-icon-setting"></span> 

                <el-button size="small" v-popover:modifyCatePopover></el-button>
                <el-button type="defalut" size="small" @click.native="handleBatchAction($event, 'batchModify')"></el-button>

                <el-dropdown>
                    <el-button icon="setting" size="small" type="">
                        <i class="el-icon-caret-bottom el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchHot', { action: 'on' })">
                            <span></span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchHot', { action: 'on', isRecommend: true })">
                            <span></span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchHot', { action: 'off' })">
                            <span></span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>

                <el-dropdown>
                    <el-button icon="setting" size="small" type="">
                        <i class="el-icon-caret-bottom el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchTips', { action: 'on', tip: '' })">
                            <span></span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchTips', { action: 'on', tip: '' })">
                            <span ></span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchTips', { action: 'off', tip: '' })">
                            <span ></span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>

                <el-dropdown>
                    <el-button icon="setting" size="small" type="">
                        <i class="el-icon-caret-bottom el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchComment', { action: 'on'})">
                            <span></span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchComment', { action: 'off'})">
                            <span></span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>

                <el-dropdown>
                    <el-button icon="setting" size="small" type="">
                        <i class="el-icon-caret-bottom el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchShare', { action: 'on'})">
                            <span ></span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchShare', { action: 'off'})">
                            <span></span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>

                <el-dropdown>
                    <el-button icon="setting" size="small" type="">
                        <i class="el-icon-caret-bottom el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchSkipAd', { action: 'on'})">
                            <span></span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchSkipAd', { action: 'off'})">
                            <span></span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>

            </div>
            <div class="ibox-content">
                <div class="control">
                    <el-table :data="filterListDate"
                              @selection-change="handleSelectCheckbox"
                              style="width: 100%"
                              element-loading-text=""
                              v-loading="loading">
                        <el-table-column type="expand">
                            <template scope="props">
                                <el-form label-position="left"
                                         inline
                                         class="props-table-expand">
                                    <el-form-item label="">
                                        <span>{{ props.row.share_count }}</span>
                                    </el-form-item>
                                    <el-form-item label="">
                                        <span>{{ props.row.create_time }}</span>
                                    </el-form-item>
                                    <el-form-item label="">
                                        <span>{{ props.row.picker_name + '\n' + props.row.picker_id }}</span>
                                    </el-form-item>
                                </el-form>
                            </template>
                        </el-table-column>
                        <el-table-column type="selection"
                                         width="35px">
                        </el-table-column>

                        <el-table-column label=" ID"
                                         prop="id"
                                         width="80">
                        </el-table-column>

                        <el-table-column label=""
                                         width="80px" class-name="cell-cover cell-badge">
                            <template scope="scope">
                                <div class="covers" @click="showPreviewCover($event, scope.row)">
                                    <img v-for="cover in scope.row.cover"
                                        :src="(cover) + '?imageView2/1/w/30/h/30'"
                                        width="30" height="30" alt="">
                                </div>
                                <div class="content-badge">
                                    <el-tag v-text="['', '', '', '', '', '', ''][scope.row.cover_show_type]"></el-tag>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column label=""
                                         min-width="180"
                                         class-name="cell-badge">
                            <template scope="scope">
                                <a
                                        v-text="scope.row.title"
                                        :title="scope.row.title"
                                        :href="getContentLink(scope.row)"
                                        target="_blank"
                                        class="font-fa"></a>
                                <div class="content-badge">
                                    <el-tag v-show="scope.row.is_top == 2" type="primary">top</el-tag>
                                    <el-tag v-show="scope.row.is_hot == 2" :type="scope.row.is_hot_promote ? 'danger': 'primary'">hot</el-tag>
                                    <el-tag v-show="scope.row.tips" color="#1C75BF">{{ scope.row.tips }}</el-tag>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column label=""
                                         prop="statusName"
                                         width="60px">
                        </el-table-column>
                        <el-table-column label=""
                                         prop="typeName"
                                         width="80px">
                        </el-table-column>
                        <el-table-column label=""
                                         prop="source_name"
                                         width="140px">
                        </el-table-column>
                        <el-table-column label=""
                                         prop="read_count"
                                         width="80px">
                        </el-table-column>
                        <el-table-column label=""
                                         width="80px">
                            <template scope="scope">
                                <router-link :to="{ name: 'CommentManage', query: { 'content_id': scope.row.id, 'status': COMMENT.PASS_CANDIDATE }}">
                                    {{scope.row.comment_count}}
                                </router-link>
                            </template>
                        </el-table-column>
                        <el-table-column label=""
                                         width="100px">
                            <template scope="scope">
                                <span>{{ scope.row.admin_member_nickname + '(ID:' + scope.row.admin_member_id + ')' }}</span>
                            </template>
                        </el-table-column>
                        <el-table-column label=""
                                         width="150px"
                                         prop="publish_time">
                        </el-table-column>
                        <el-table-column label="" width="280px">
                            <template scope="scope">
                                <router-link v-show="filter.content_type == 1" :to="'content-edit?id=' + scope.row.id">
                                    <el-button
                                            size="small"
                                            icon="edit"
                                            type="default">
                                    </el-button>
                                </router-link>

                                <el-button
                                        v-if="scope.row.status == 2"
                                        v-text=" scope.row.is_hot == 2 ? '' : '' "
                                        size="small"
                                        type="info"
                                        @click="handleSetHot($event, scope.$index, scope.row)">
                                </el-button>

                                <el-button
                                        v-if="scope.row.status != 3"
                                        size="small"
                                        type="danger"
                                        @click="handleDelete($event, scope.$index, scope.row)">
                                </el-button>

                                <el-dropdown>
                                    <el-button size="small" type="">
                                        <i class="el-icon-caret-bottom el-icon--right"></i>
                                    </el-button>
                                    <el-dropdown-menu slot="dropdown">

                                        <el-dropdown-item v-if="scope.row.status == 2">
                                            <span @click="handleRefershCDN($event, scope.$index, scope.row)">cdn</span>
                                        </el-dropdown-item>

                                        <el-dropdown-item v-if="scope.row.status == 2 && scope.row.is_hot != 2">
                                            <span @click="handleSetHot($event, scope.$index, scope.row, true)"></span>
                                        </el-dropdown-item>
                                    </el-dropdown-menu>
                                </el-dropdown>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>

                <div class="control">
                    <el-pagination @current-change="handleCurrentChange"
                                   :current-page="filter.page"
                                   :page-count="totalPage"
                                   layout="total, prev, pager, next, jumper">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

    import ArticleTags from '../../widget/ArticleTags.vue';
    import moment from '../../module/MyMoment';
    import q from 'q';
//    import storeFilterMixin from 'src/mixin/storeFilterMixin';
    import PreviewCover from '../../components/preview-cover';
    import QuRequest from '../../core/QuRequest';
    import ArticleStatus from '../../module/ArticleStatus';
    import ContentModel from '../../module/Content';
    import Auth from '../../core/Auth';
//    import deepcopy from 'deepcopy';

    const quRequest = new QuRequest();

    import {
        COMMENT
    } from '../../module/CommentConfig';
//
//    import fixedTableMixin from 'src/mixin/fixedTableMixin';
//
    let contentModel = null;
//    let quRequest = null;
    let previewCover = null;

    const MODIFY_ACTIONS = {
        'batchModify': options => {
            return {
                api: quRequest.Apis.qukan.content.modifyContentStatus,
                getData(item) {
                    return {
                        id: item.id,
                        status: 3
                    }
                },
                handleSuccess(item) {
                    item.status = '3';
                },
                getConfirmMessage() {
                    return ` "" `;
                }
            }
        },
        'batchComment': options => {
            // form.can_comment, 1 | 2
            let { action } = options;
            let isOn = action === 'on';
            return {
                api: quRequest.Apis.qukan.content.modifyContent,
                getData(item) {
                    return {
                        id: item.id,
                        can_comment: isOn ? 1 : 2
                    }
                },
                handleSuccess(item) {
                    item.can_comment = isOn ? 1 : 2
                },
                getConfirmMessage() {
                    return `${ isOn ? '' : '' } "" `;
                }
            }
        },
        'batchShare': options => {
            // form.share_type: 0 | 3
            let { action } = options;
            let isOn = action === 'on'; // on | off
            let status = isOn ? '0' : '3';
            return {
                api: quRequest.Apis.qukan.content.modifyContent,
                getData(item) {
                    return {
                        id: item.id,
                        share_type: status
                    }
                },
                handleSuccess(item) {
                    item.share_type = status;
                },
                getConfirmMessage() {
                    return `${ isOn ? '' : '' } "" `;
                }
            }
        },
        'batchSkipAd': options => {
            // form.extend.skip_ad: 0 | 1
            let { action } = options;
            let isOn = action === 'on'; // on | off
            let status = isOn ? '0' : '1';
            return {
                api: quRequest.Apis.qukan.content.modifyContent,
                getData(item) {
                    let { extend = {} } = item;
                    extend = typeof extend === 'object' ? extend : {};
                    extend.skip_ad = status;
                    return {
                        id: item.id,
                        extend: JSON.stringify(extend)
                    }
                },
                handleSuccess(item) {
                    item.skip_ad = status;
                },
                getConfirmMessage() {
                    return `${ isOn ? '' : '' } "" `;
                }
            }
        },
        'batchTips': options => {
            let { action } = options;
            let isOn = action === 'on';
            return {
                api: quRequest.Apis.qukan.content.setTips,
                getData(item) {
                    return {
                        id: item.id,
                        tips: options.tip
                    }
                },
                handleSuccess(item) {
                    item.tips = options.tip;
                },
                getConfirmMessage() {
                    return `${ isOn ? '' : '' } "${ options.tip || '' }" `;
                }
            }
        },
        'batchHot': options => {
            let { action, isRecommend } = options;
            let isOn = action === 'on';
            let status = isOn ? 2 : 1;
            let type = isRecommend ? 255 : -1;
            return {
                api: quRequest.Apis.qukan.content.setHot,
                getData(item) {
                    return {
                        content_id: item.id,
                        status: status,
                        type: type
                    }
                },
                handleSuccess(item) {
                    item.is_hot = status;
                },
                getConfirmMessage() {
                    if (!isOn) {
                        return ` "" `;
                    } else {
                        return ` "${ isRecommend ? '' : '' }" `
                    }
                }
            }
        },
        'batchEditCategory': options => {
            let { modifyCate, articleTypesMap } = options;
            return {
                api: quRequest.Apis.qukan.content.modifyContent,
                getData(item) {
                    return {
                        id: item.id,
                        type: modifyCate.type,
                    }
                },
                handleSuccess(item) {
                    item.type = modifyCate.type;
                },
                getConfirmMessage() {
                    return ` "${ articleTypesMap[modifyCate.type] }" `;
                }
            }
        }
    };

    class BatchAction {
        constructor({ api, getData, handleSuccess, getConfirmMessage }) {
            return {
                create(item) {
                    return quRequest.send(api, getData(item)).then(res => {
                        if (res.code == 0) {
                            handleSuccess&& handleSuccess(item)
//                            _.isFunction(handleSuccess) ? handleSuccess(item) : '';
                        } else {
                            throw new Error(res.message || '')
                        }
                    });
                },
                confirmMessage: getConfirmMessage
            };
        }
    }

    export default {
//        mixins: [fixedTableMixin, storeFilterMixin],
        components: {
            ArticleTags
        },
        created() {
//            this.$restoreFilterStore();
            contentModel = new ContentModel()
        },
        mounted() {
            previewCover = new PreviewCover({
                autoClose: true,
                container: 'body'
            });
//            quRequest = new QuRequest();
            this.getList();
            this.getCategorys();
        },
        beforeRouteLeave(to, from, next) {
            previewCover.destory();
            next(true);
        },
        data() {
            return {
                expandAll: true,
                loading: false,
                COMMENT: COMMENT,
                filterStoreKey: 'content-filter-store',
                stat: {},
                list: [],
                articleTypes: [],
                articleTypesMap: {},
              articleStatus: ArticleStatus.maps,
                articleDelay: {
                    "1": "",
                    "2": "",
                },
                articleTips: ["", ""],
                control: {
                    currentItem: {}
                },
                filter: {
                    show_type: '2,3',
                    content_type: '1',
                    id: '',
                    status: "",
                    title: "",
                    type: "",
                    tag: "",
                    tips: "",
                    is_delay: "",
                    // 1 id 2   1
                    order_type: "1",
                    // 1  2   1
                    order: "1",
                    is_hot: "0",
                    page: 1,
                    source_name: '',
                    admin_member_id: '',
                    is_top: '0', // . 0, 2,
                    source_name_type: '0' // 0: 1: 2: 3:
                },
                listData: [],
                totalPage: 1,
                listSelect: [],
                modifyCate: {
                    type: "",
                    visible: false
                }
            }
        },
        computed: {
            filterListDate() {
                return this.listData.map((item) => {
                    item.publish_time = moment(item.publish_time).toFormat();
                    item.create_time = moment(item.create_time).toFormat();
                    item.typeName = this.articleTypesMap[item.type];
                    item.statusName = this.articleStatus[item.status];
                    return item;
                });
            },
            sourceNameTypes() {
                return contentModel.sourceNameTypes;
            }
        },
        methods: {
            filterAction: function ($event) {
                this.filter.page = 1;
                this.getList();
            },
            creatForm(){
                let {filter} = this;
                let arry = [];
                for( let key in filter) {
                    let value = filter[key];
                    arry.push(`<input name="${key}" value="${ value }">`);
                }
                return arry.join('');
            },
            handleExportData($event) {
                let filter = Object.assign({},this.filter);
                filter.export = 1;
                filter.token = Auth.getToken();
                let url = quRequest.getUrl(quRequest.Apis.qukan.content.getAdminContentList);
                let formHtml = `<form method="post" target="_blank" action="${ url }">
                    ${
                        this.creatForm()
                    }
                </form>`;
                let $container = document.createElement('div');
                $container.innerHTML = formHtml;
                document.body.appendChild($container);
                $container.querySelector('form').submit();
                $container.remove();
            },
            showPreviewCover: function ($event, item) {
                previewCover.show($event.target, item.cover, {
                    top: -60,
                    left: 40
                });
            },
            handleCurrentChange(page) {
                this.filter.page = page;
                this.getList();
            },
            handleSelectCheckbox (select) {
                this.listSelect = select;
            },
            handleDelete($event, index, item) {
                var $elem = $event.target;
                let {$blockRequest, $message} = this;
                $blockRequest({
                    $elem: $elem,
                    req: quRequest.send(quRequest.Apis.qukan.content.modifyContentStatus, {
                        id: item.id,
                        status: "3",
                    }),
                    pendingText: ''
                }).then(res => {
                    $message.success(` ${item.title.substring(0, 10)}... `);
                    item.status = "3";
                }).catch(reason => {
                    $message.error(`${reason.message || ''} `);
                    throw reason;
                }).done();
            },
            handleRefershCDN($event, $index, item) {
                let {$blockRequest, $message} = this;
                let $elem = $event.target;

                $blockRequest({
                    $elem: $elem,
                    req: quRequest.send(quRequest.Apis.qukan.content.refreshCND, {
                        id: item.id
                    })
                }).then(res => {
                    $message.success('');
                }).catch(reason => {
                    $message.error('');
                    throw reason;
                }).done();
            },
            handleSetHot ($event, $index, item, isRecommend = false) {
                let {$blockRequest, $message} = this;
                let $elem = $event.target;
                let isHot = item.is_hot;
                let changeStatus = isHot == 2 ? 1 : 2;

                return this.$confirm(`${ changeStatus == 2 ? '' : '' }, ?`, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    return $blockRequest({
                        $elem: $elem,
                        req: quRequest.send(quRequest.Apis.qukan.content.setHot, {
                            content_id: item.id,
                            status: changeStatus,
                            type: isRecommend ? 255 : -1,
                        })
                    });
                })
                .then(res => {
                    $message.success('');
                    item.is_hot = changeStatus;
                }).catch(reason => {
                    $message.error('');
                    throw reason;
                })
            },
            handleBatchAction($event, action, options = {}) {
                let { $message, listSelect } = this;
                let $elem = $event.target;

                return q.fcall(() => {
                    if (!listSelect.length || $elem._req_block) return false;
                    // ACTION_HANDLES[action](options)
                    let hadle = new BatchAction(MODIFY_ACTIONS[action](options));

                    return this.$confirm(`${ hadle.confirmMessage() }, ?`, '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        let promises = listSelect.map(item => {
                            return hadle.create(item);
                        });
                        $message.info('...');
                        return q.allSettled(promises).then(values => {
                            let [ handelSuccess, handleError ] = [[], []];
                            let messages = [];

                            values.forEach((v) => {
                                if (v.state === "fulfilled") {
                                    handelSuccess.push(v);
                                } else {
                                    messages.push(v.reason.message);
                                    handleError.push(v);
                                }
                            });

                            if (messages.length) {
                                const h = this.$createElement;
                                this.$notify({
                                    title: '',
                                    message: h('div', { domProps: { innerHTML: messages.join('<br/>') } }),
                                    duration: 10 * 1000
                                })
                            }
                            return { handleError, handelSuccess };
                        });
                    }, () => {
                        $message.info('');
                    });
                }).then(({ handleError, handelSuccess } = {}) => {
                    if (handleError || handelSuccess) {
                        $message.info(`${ handelSuccess.length }${ handleError.length }`);
                    }
                }).catch(error => {
                    this.$notify.error({
                        title: '',
                        message: error.message,
                        duration: 0
                    });
                    throw error;
                })
                .fin(function () {
                    $elem._req_block = false;
                })
                .done();
            },
            getContentLink(item) {
                let url = item.assign_url || item.url || 'javascript:;'
                let isAd = item.extend && item.extend.skip_ad && item.extend.skip_ad == 1
                return `${url}?${isAd ? 'skip_ad=1' : ''}`;
            },
            getList() {
                let {filter} = this
//                this.$updateFilterStore();
                this.loading = true;
                quRequest.send(quRequest.Apis.qukan.content.getAdminContentList, filter)
                    .then(res => {
                        let data = res.data;
                        this.listData = data.data || [];
                        this.totalPage = data.total_page || 1;
                    })
                    .catch(err => {
                        this.$message.error(`${ err.message || '' }`);
                    })
                    .fin(() => {
                        this.loading = false;
                    }).done();
            },
            getCategorys() {
                quRequest.send(quRequest.Apis.qukan.content.getDefaultChannelList)
                    .then(res => {
                        let data = res.data
                        this.articleTypesMap = {};
                        data.forEach((type) => {
                            this.articleTypesMap[type.id] = type.name;
                        });
                        this.articleTypes = data;
                    })
                    .catch(err => {
                        this.$message.error(`${ err.message || '' }`);
                        throw err;
                    }).done();
            }
        }
    }

</script>

<style lang="scss" type="text/scss">
    .ibox{
        margin-bottom: 25px;
        margin-top: 0;
        border-radius: 5px;
        box-shadow: 0 2px 3px hsla(0,0%,4%,.1), 0 0 0 1px hsla(0,0%,4%,.1);
        display: block;
        overflow: hidden;
        .ibox-content{
            .control{
                &:last-child{
                    margin-top:0.75rem;
                    .el-pagination{
                        padding:0;
                    }
                }
            }
        }
    }
    .el-tag {
        cursor: pointer;
    }
    .ibox-filter {
        .el-tag {
            margin-right: 6px;
        }
    }
    .content-manage-box {
        .el-table__body-wrapper {
            z-index: 0;
        }

        .filter-item {
            display: inline-block;
            margin-right: 20px;
        }

        .cell-cover {
            position: relative;
            .covers {
                height: 40px;
                img {
                    position: absolute;
                    border-radius: 3px;
                    border: 1px #dfdfdf solid;
                    box-shadow: 0 0 3px rgba(0, 0, 0, .125);
                    &:nth-child(1) {
                        left: 0;
                        z-index: 100;
                    }
                    &:nth-child(2) {
                        left: 6px;
                        z-index: 99;
                    }
                    &:nth-child(3) {
                        left: 12px;
                        z-index: 98;
                    }
                    &:nth-child(4) {
                        left: 18px;
                        z-index: 97;
                    }
                }
            }
        }

        .props-table-expand {
            font-size: 0;
            label {
                width: 90px;
                color: #99a9bf;
            }
            .el-form-item {
                margin-right: 0;
                margin-bottom: 0;
                width: 30%;
            }
        }

        .el-table {
            .cell {
                padding: 18px 5px 10px;
                .el-button {
                    margin-left: 10px;
                    margin-top: 5px;
                }

                .el-button + .el-button {
                    margin-left: 10px;
                }
            }
        }

        .cell-badge {
            position: relative;
            .content-badge {
                position: absolute;
                top: -2px;
                right: 5px;
            }
            .el-tag {
                height: 18px;
                line-height: 16px;
            }
        }
    }
</style>



