<template>
    <el-select
        :size="size"
        @change="updateValue($event)"
        :placeholder="placeholder"
        :value="value"
        v-model="selector">
        <el-option label="" value=""></el-option>
        <el-option v-for="item in options"
                   :label="item"
                   :value="item"
                   :key="item">
        </el-option>
    </el-select>
</template>

<script>
const tags = {
    "article": {
        "9": {
            "tags": ["", "", "", "", ""]
        },
        "4": {
            "tags": ["", "", ""]
        },
        "14": {
            "tags": ["", "", ""]
        },
        "7": {
            "tags": ["", "", ""]
        },
        "18": {
            "tags": ["", ""]
        },
        "5": {
            "tags": ["", "", ""]
        },
        "3": {
            "tags": ["", "", "", "", ""]
        },
        "17": {
            "tags": ["", "", "", ""]
        },
        "8": {
            "tags": ["", "", ""]
        },
        "16": {
            "tags": ["", "", "", ""]
        },
        "12": {
            "tags": ["", "", "", ""]
        },
        "10": {
            "tags": ["", "", "", "", ""]
        },
        "11": {
            "tags": ["", "", ""]
        },
        "6": {
            "tags": ["", "", "", "", ""]
        },
        "13": {
            "tags": ["", "", ""]
        },
        "2": {
            "tags": ["", "", "", ""]
        },
        "15": {
            "tags": ["", "", "", ""]
        }
    },
    "video": {
        "2": {
            "tags": ["", "", "", "", ""]
        },
        "6": {
            "tags": ["", "", "", "", ""]
        },
        "19": {
            "tags": ["", ""]
        },
        "7": {
            "tags": ["", "", ""]
        },
        "1": {
            "tags": ["", "", ""]
        }
    }
}


export default {
    props: {
        type: {},
        category: {},
        placeholder: {},
        value: {},
        size: {
            type: String,
            default: 'small'
        }
    },
    data() {
        return {
            selector: ''
        }
    },
    mounted () {
        this.selector = this.value;
    },
    computed: {
        options () {
            let { type, category } = this;
            let obj = tags[type][category];
            this.selector = "";
            if (obj && obj.tags) {
                return obj.tags
            }
            return [];
        }
    },
    methods: {
        updateValue (value) {
            this.$emit('input', value);
            this.$emit('change', value);
        }
    }
}
</script>



